.section-navigation {
  padding: $padding-md 0;
  //   background-color: lightcyan;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3);
  .container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    align-items: center;
  }
}

.nav__tabs {
  width: 50%;
  display: flex;
  justify-content: space-between;

  &-list {
    width: 80%;
    display: flex;
    justify-content: space-between;

    li.active {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 4px;
        background-color: dodgerblue;
        bottom: -160%;
        left: 0;
      }
    }
  }
}
